.background-gradient {
  background: linear-gradient(360deg, #121212 0%, #242424 100%) #121212;
  border-radius: 10px;
}

.background-gradient-before {
  &::before {
    content: '';
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    position: absolute;
    background: linear-gradient(360deg, #121212 0%, #242424 100%) #121212;
    border-radius: 10px;
  }
}

.background-many-gradient {
  background: linear-gradient(360deg, #121212 0%, #242424 100%) #121212;
  border-radius: 10px;
  position: relative;
  z-index: 0;
  &::before, &::after {
    content: '';
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    border-radius: 10px;
    background: linear-gradient(360deg, #121212 0%, #242424 100%) #121212;
  }

  &::before {
    z-index: -1;
    opacity: 0.4;
    height: calc(100% - 40px);
    // height: min(calc(100% - 40px), 99.83%);
    width: calc(100% + 30px);
    @include media-breakpoint-up('sm') {
      width: calc(100% + 40px);
    }
  }

  &::after {
    z-index: -2;
    opacity: 0.2;
    height: calc(100% - 80px);
    width: calc(100% + 60px);
    // height: clamp(calc(100% - 80px), 99.66%, calc(100% - 160px));
    @include media-breakpoint-up('sm') {
      width: calc(100% + 80px);
    }
  }
}

.background-many-gradient--one-layout {
  &::after {
    display: none;
  }
}

.background-many-gradient.background-many-gradient--small {
  &::before {
    height: calc(100% - 18px);
    width: calc(100% + 18px);
  }
  &::after {
    height: calc(100% - 36px);
    width: calc(100% + 36px);
  }
}

.background-many-gradient--circle {
  border-radius: 50%;
  &::before, &::after {
    border-radius: 50%;
  }
  &::before {
    height: calc(100% + 30px);
    @include media-breakpoint-up('sm') {
      height: calc(100% + 40px);
    }
  }
  &::after {
    height: calc(100% + 60px);
    @include media-breakpoint-up('sm') {
      height: calc(100% + 80px);
    }
  }
}

.background-many-gradient.background-many-gradient--small.background-many-gradient--box-md {
  @include media-breakpoint-up('md') {
    margin-left: 18px;
    margin-right: 18px;
  }
}