.section {

}

.section--full-height {
  display: flex;
  &::before {
    content: '';
    height: 100vh;
    width: 0;
    flex: 0 0 auto;
    display: block;
  }
  align-items: center;
}
.section--center {
  text-align: center;
}

.section--mb {
  margin-bottom: 12.5vw;
  @media (min-width: 1375px) {
    margin-bottom: 110px;
  }
}