.row-pay {
  &__wrapper {
    margin-top: (30rem/16);
    @include media-breakpoint-up('sm') {
      margin-top: 2.625rem;
    }
  }

  &__item {
    padding: 30px 15px;
    height: 100%;
    max-width: 270px;
    margin-right: auto;
    margin-left: auto;
    @include media-breakpoint-up('sm') {
      max-width: none;
      margin-left: 0;
      margin-right: 0;
    }
    @include media-breakpoint-up('md') {
      max-width: 300px;
    }
    @include media-breakpoint-up('lg') {
      max-width: none;
    }
    & > * {
      max-width: (160rem/16);
      margin-left: auto;
      margin-right: auto;
    }
  }

  &__wrapper:nth-child(2n + 1) &__item {
    @include media-breakpoint-up('md') {
      margin-left: auto;
    }
    @include media-breakpoint-up('lg') {
      margin-left: 0;
    }
  }
}