.social-list {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  padding-left: 0;
  margin-bottom: 1.7em;
  &__item {
    margin: 0 auto;
    padding: 5px;
    display: flex;
    align-items: center;
    svg, img {
      max-height: 20px;
      margin-right: 7px;
    }
  }
}

.social-list--width-100 {
  width: 100%;
}