.carousel-offers {
  max-width: 780px;
  margin-left: auto;
  margin-right: auto;
  padding-top: 50px;
  .splide__track {
    overflow: visible;
  }
  .splide__list {
    align-items: center;
  }

  &__item {
    padding: 30px 20px;
    display: flex;
    justify-content: center;
    .row {
      width: 100%;
      max-width: 670px;
    }
  }

  &__img {
    position: relative;
    text-align: center;
    img, svg {
      display: none;
      @include media-breakpoint-up('md') {
        display: block;
        position: absolute;
        bottom: 0;
        left: 50%;
        transform: translate(-50%, 0);
        max-height: calc(100% + 85px);
        max-width: calc(100% - 30px);
      }
    }
  }

  &__img-mob {
    margin-top: 20px;
    margin-bottom: 30px;
    @include media-breakpoint-up('md') {
      display: none;
    }
  }

  &__text {
    text-align: center;
    @include media-breakpoint-up('md') {
      text-align: left;
    }
    p {
      text-align: left;
    }
    .btn {
      margin-top: 1em;
      @include media-breakpoint-up('md') {
        margin-top: 0.3em;
      }
    }
  }

  .splide__pagination__page {
    width: 8px;
    height: 8px;
    background: $color-grey;
    margin: 3px 8px;
    transition: background-color $transition;
    position: relative;
    &::before {
      content: '';
      position: absolute;
      left: -3px;
      right: -3px;
      top: 0;
      height: 100%;
    }

    &.is-active {
      transform: scale(1);
      background: $color-white;
    }
  }

  .splide__pagination {
    position: static;
    margin-top: 38px;
    transform: translate(0, 0);
  }

  .splide__arrows {
    position: absolute;
    top: 50%;
    width: calc(100% + 120px + 2vw);
    left: 50%;
    transform: translate(-50%, 0);
    max-width: 100vw;
    z-index: 10;
  }

  .splide__arrow {
    border-radius: 0;
    background: none;
    font-size: 30px;
    opacity: 1;
    width: 1em;
    height: 1em;
    align-items: center;
    line-height: 1em;

    &--prev {
      transform: translateY(-50%) rotate(180deg);
      left: 0;
    }

    &--next {
      right: 0;
    }
  }

  .splide__slide {
    margin-right: calc(90px + 2vw);
    transition: opacity $transition;
    opacity: 0.4;

    .btn {
      transition: background-color $transition, background-image $transition, color $transition, box-shadow $transition;
    }
    &.is-active {
      opacity: 1;
    }
  }
}