a {
  text-decoration: none;
  border-bottom: 1px solid;
  color: inherit;
  &:hover {
    color: inherit;
    text-decoration: none;
    border-color: transparent;
  }
}

a.like-text {
  color: inherit;
  transition: opacity $transition;
  border-bottom: 0;
  &:hover {
    color: inherit;
    opacity: 0.6;
  }
}