.row-path {
  display: inline-flex;
  position: relative;
  padding-left: 32px;
  padding-top: (40em/16);
  padding-bottom: (40em/16);
  flex-direction: column;

  &::before {
    content: '';
    height: 100%;
    width: 2px;
    background-image: linear-gradient(0deg, rgba($color-white, 0) 0%, rgba($color-white, 1) 50%, rgba($color-white, 0) 100%);
    position: absolute;
    bottom: 0;
    left: 39px;
  }

  &__item {
    display: flex;
    padding-left: 32px;
    position: relative;
    text-align: left;
    &::before {
      content: '';
      width: 16px;
      height: 16px;
      border-radius: 50%;
      position: absolute;
      top: 0;
      left: 0;
      border: 2px solid $color-white;
      z-index: 1;
      background: $color-background;
    }
    &::after {
      content: '';
      position: absolute;
      top: 5px;
      left: 5px;
      width: 6px;
      height: 6px;
      background: $color-accent;
      border-radius: 50%;
      z-index: 2;
    }
  }

  &__item + &__item {
    margin-top: (20em/16);
  }

  &__num {
    position: absolute;
    top: 0;
    left: -32px;
    line-height: 1;
  }
}

@mixin row-path--desctop {
  flex-direction: row;
  padding-top: 0;
  padding-bottom: 0;
  padding-left: (20em/16);
  padding-right: (40em/16);
  margin-bottom: 6.245%;
  &::before {
    left: 0;
    height: 2px;
    width: 100%;
    background-image: linear-gradient(90deg, rgba($color-white, 0) 0%, rgba($color-white, 1) 50%, rgba($color-white, 0) 100%);
    bottom: 8px;
  }
  &::after {
    content: '';
    position: absolute;
    left: 0;
    right: 0;
    padding-top: 6.245%;
    top: 100%;
    margin-top: -5px;
    background: url('../img/long-arrow.svg') no-repeat top center;
    background-size: contain;
  }
  .row-path__item {
    flex: 3 1 0%;
    margin-left: (20em/16);
    padding-left: 0;
    padding-bottom: 26px;
    &:before {
      top: auto;
      bottom: 0;
      left: -7px;
    }
    &::after {
      top: auto;
      bottom: 5px;
      left: -2px;
    }
  }
  .row-path__item:last-child {
    flex: 2 1 0%;
  }
  .row-path__item + .row-path__item {
    margin-top: 0;
  }

  .row-path__num {
    top: calc(100% + 16px);
    left: -7px;
    z-index: 2;
  }
}

.row-path--lg {
  @include media-breakpoint-up('lg') {
    @include row-path--desctop;
  }
}