  border-radius: 0;
  background: none;
  font-size: 30px;
  opacity: 1;
  width: 1em;
  height: 1em;
  align-items: center;
  line-height: 1em;
}

.carousel-offers .splide__arrow--prev {
  transform: translateY(-50%) rotate(180deg);
  left: 0;
}

.carousel-offers .splide__arrow--next {
  right: 0;
}

.carousel-offers .splide__slide {
  margin-right: calc(90px + 2vw);
  transition: opacity 0.2s linear;
  opacity: 0.4;
}

.carousel-offers .splide__slide .btn {
  transition: background-color 0.2s linear, background-image 0.2s linear, color 0.2s linear, box-shadow 0.2s linear;
}

.carousel-offers .splide__slide.is-active {
  opacity: 1;
}

.footer {
  display: flex;
  align-items: flex-end;
  padding-top: calc(0.9375em);
  padding-bottom: calc(20px + 0.9375em);
  font-size: 12px;
}

@media (min-width: 36rem) {
  .footer {
    font-size: 1rem;
  }
}

.footer__copy {
  align-self: center;
  margin-right: 1em;
  flex: 1 1 50%;
}

.footer__logo {
  flex: 0 1 auto;
  max-width: 50%;
  height: 1.25em;
  display: flex;
  align-items: center;
  margin-left: auto;
  justify-content: flex-end;
}

.footer__logo img, .footer__logo svg {
  max-height: 3.125em;
}
