.footer {
  display: flex;
  align-items: flex-end;
  padding-top:  calc(#{1em * $line-height * 0.75});
  padding-bottom: calc(20px + #{1em * $line-height * 0.75});
  font-size: 12px;
  @include media-breakpoint-up('sm') {
    font-size: $text-size;
  }

  &__copy {
    align-self: center;
    margin-right: 1em;
    flex: 1 1 50%;
  }

  &__logo {
    flex: 0 1 auto;
    max-width: 50%;
    height: 1em * $line-height;
    display: flex;
    align-items: center;
    margin-left: auto;
    justify-content: flex-end;
    img, svg {
      max-height: 1em * $line-height * 2.5;
    }
  }
}