.form-vertical {
  display: flex;
  flex-direction: column;
  align-items: center;
  input {
    width: 100%;
  }
}

.form-small {
  max-width: 370px;
  padding: (30rem/16);
}

.form-custom-1 {
  margin-top: 30px;
  position: relative;
  @media (min-width: 550px) {
    margin-top: 75px;
  }
  @include media-breakpoint-up('lg') {
    margin-bottom: 100px;
  }

  &__imgs {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: -1;
    display: none;
    @media (min-width: 550px) {
      display: block;
    }
  }
  &__img {
    position: absolute;
    pointer-events: none;
    max-width: none;
  }

  &__img--left {
    bottom: 0;
    left: (40rem/16);
    transform: translate(-82%, 16%);
  }

  &__img--right {
    z-index: -1;
    opacity: 0.8;
    top: (30rem/16);
    right: 0;
    transform: translate(67%, 0%) scaleX(-0.75) scaleY(0.75);
    transform-origin: 50% 0%;
  }

  &__decors {
    display: none;
    @media (min-width: 550px) {
      display: block;
    }
  }

  &__decor {
    position: absolute;
    top: 50%;
    transform: translate(0, -50%);
    pointer-events: none;
    z-index: -1;
  }
  &__decor--01 {
    right: 103%;
  }
  &__decor--02 {
    left: 93%;
  }
}