html {
  font-size: $text-size;
}
body {
  font-size: 100%;
  font-family: $font-family;
  font-weight: $font-weight;
  line-height: (20/16);
}

//normalize 
.ul-normalize {
  padding-left: 0;
  list-style: none;
}

//title and text
.h2{
  text-transform: uppercase;
  font-size: (24rem/16);
  font-weight: 400;
  line-height: (34/24);
  text-align: center;
  letter-spacing: 0.1em;
  margin-bottom: $text-size;
  @include media-breakpoint-up('lg') {
    font-size: (28rem/16);
  }
}
.h3 {
  font-size: (24rem/16);
  font-weight: 400;
  line-height: (29/24);
  margin-bottom: $text-size;
}

.text-grey {
  color: $color-text-grey;
}
.text-transparent {
  color: transparent;
}
.text-center {
  text-align: center;
  margin-left: auto;
  margin-right: auto;
}

p {
  max-width: 55.375em;
  margin-bottom: $text-size;
}

ul, ol {
  margin-bottom: $text-size;
}

ul, ol, li, p, h1, h2, h3, h4, h5, h6, .h1, .h2, .h3, .h4, .h5, .h6,
input, .input {
  &:last-child:not(.save-margin) {
    margin-bottom: 0;
  }
} 

//list-mod
.list-icons {
  li {
   position: relative;
   padding-left: 28px;
  }

  li + li {
    margin-top: 9px;
  }

  &__icon {
    position: absolute;
    top: 50%;
    width: auto;
    height: auto;
    max-height: 19px;
    max-width: 25px;
    right: calc(100% - 9.5px);
    transform: translate(50%, -50%);
  }
}

