.header {
  display: flex;
  align-items: center;
  padding-top: 1em;
  padding-bottom: 1em;
  position: relative;
  @include media-breakpoint-up('lg') {
    padding-top: (30em/16);
    padding-bottom: (20em/16);
  }
  &__wrapper {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    z-index: $z-index;
  }
  &__logo {
    max-width: 130px;
    margin-right: auto;
    flex: 0 0 auto;
    @include media-breakpoint-up('lg') {
      max-width: 180px;
    }
    @include media-breakpoint-up('xl') {
      max-width: 220px;
    }
  }

  &__burger {
    width: 18px;
    height: 11px;
    position: relative;
    margin-right: 10px;
    z-index: $z-index-menu;
    transition: transform $transition-menu;
    @include media-breakpoint-up('lg') {
      display: none;
    }
    &::before {
      content: '';
      position: absolute;
      top: -5px;
      left: -5px;
      right: -5px;
      bottom: -5px;
      display: block;
    }
    div {
      height: 1px;
      background: $color-text;
      position: absolute;
      width: 100%;
      top: 5px;
      left: 0;
      transition: transform $transition-menu;
    }
    div:first-child {
      top: 0;
    }
    div:last-child {
      top: auto;
      bottom: 0;
    }

    &.active {
      transform: rotate(180deg);
      div:first-child {
        transform: translate(0, 5px) rotate(-45deg);
      }
      div:last-child {
        transform: translate(0, -5px) rotate(45deg);
      }
      div {
        transform: scaleX(0);
      }
    }
  }

  &__links {
    display: flex;
    white-space: nowrap;
    flex: 1 0 auto;
    flex-direction: column;
    top: 100%;
    left: 0;
    z-index: $z-index-menu;

    transition: opacity $transition-menu, visibility 0s $transition-menu-time, transform $transition-menu;
    position: absolute;
    background: $color-background;
    padding: 1em;
    opacity: 0;
    visibility: hidden;
    transform: translateY(50px);
    
    @include media-breakpoint-up('lg') {
      align-items: center;
      justify-content: space-between;
      flex-direction: row;
      margin-right: 2em;
      margin-left: 2em;
      max-width: 560px;

      transition: opacity $transition-menu, visibility $transition-menu-time 0s, transform $transition-menu;
      position: relative;
      background: none;
      padding: 0;
      opacity: 1;
      visibility: visible;
      transform: translateY(0);
      
    }

    &.active {
      transition: opacity $transition-menu, visibility $transition-menu-time 0s, transform $transition-menu;
      opacity: 1;
      visibility: visible;
      transform: translateY(0);
    }

    &__item {
      text-align: center;

      @include media-breakpoint-up('lg') {
        margin: 0;
      }
    }

    &__item--lang {
      display: flex;
      align-items: center;
      justify-content: center;

      @include media-breakpoint-up('lg')  {
        display: none;
      }
    }

    a {
      padding: 0.5em 1em;
      display: block;
      @include media-breakpoint-up('lg') {
        padding: 5px;
      }
    }
  }

  &__links &__language {
    @include media-breakpoint-up('lg')  {
      display: none;
    }
  }

  &__social {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-left: auto;
    flex: 0 0 auto;
    &__item {
      flex: 0 0 auto;
    }

    a {
      padding: 5px;
    }

    &__item + &__item {
      @include media-breakpoint-up('sm') {
        margin-left: calc(1em - 10px);
      }
      @include media-breakpoint-up('xl') {
        margin-left: calc(1.25em - 10px);
      }
    }
  }

  &__language {
    margin-left: 1em;
    position: relative;
    @include media-breakpoint-up('xl')  {
      margin-left: (20em/16);
    }

    &__curent {
      position: relative;
      padding-right: 16px;
      &:after {
        content: '';
        position: absolute;
        top: 50%;
        right: 0;
        border-width: 8px 5px 0em 5px;
        border-color: $color-text transparent transparent transparent;
        border-style: solid;
        transform: translate(0, -50%);
        transition: transform $transition-menu;
      }

      &.active {
        &::after {
          transform: translate(0, -50%) rotate(180deg);
        }
      }
    }

    &__modal {
      position: absolute;
      top: 110%;
      right: 0;
      background: $color-background;
      min-width: 80px;
      text-align: center;
      
      opacity: 0;
      visibility: hidden;
      transform: translateY(50px);
      transition: opacity $transition-menu, visibility 0s $transition-menu-time, transform $transition-menu;
      
      &.active {
        transition: opacity $transition-menu, visibility $transition-menu-time 0s, transform $transition-menu;
        opacity: 1;
        visibility: visible;
        transform: translateY(0);
      }
      a {
        display: block;
        padding: 0.5em 1em;
      }
    }
  }

  & > &__language {
    display: none;
    @include media-breakpoint-up('lg') {
      display: block;
    }
  }
}